import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl, NgControlStatus } from '@angular/forms';
import { DataService } from 'src/app/Services/provider.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  passwordForm: FormGroup;
  responseData: any;

  constructor(public service: DataService, private route: ActivatedRoute,public navi:Router) {
    this.passwordForm = new FormGroup({
      // oldPassword: new FormControl('', [Validators.required, Validators.minLength(8)]),
      newPassword: new FormControl('', [Validators.required, Validators.minLength(8)]),
      confirmPassword: new FormControl('')
    })
  }

  ngOnInit() {
    console.log("qqwertyui", this.route.queryParams['value'].email)
    console.log("qqwertyui", this.route.queryParams['value'].forgotToken)
    localStorage.clear()
    this.verifyLink(this.route.queryParams['value'].email,this.route.queryParams['value'].forgotToken)
  }
  save(val) {

    console.log("#############33",this.responseData)
    let changeData =
    {
      "email":this.route.queryParams['value'].email,
      "newPassword": val.newPassword,
      "lang":"en"
    }
    console.log(changeData)
    this.service.postApi('user/reset', changeData,0).subscribe(response => {
      console.log("!@%^&$#^&^&")
      this.responseData = response;
      console.log("succ===>", JSON.stringify(this.responseData))
      console.log(`Login Data====>${JSON.stringify(val)}`);
      if (response['statusCode'] == 200) {
        this.service.showSuccess(response['statusMessage']);
        this.ngOnInit()
      }
      else {
        console.log("else")
        this.service.showError('DONOT UPDATE')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })

    // this._location.back();

  }
  verifyLink(email,forgotToken){
   let temp={
     email:email,
     forgotToken:forgotToken,
     lang:'en'
   } 
    this.service.postApi('user/verifyLink',temp,0).subscribe(response=>{
      this.responseData=response['email']
      if (response['statusCode'] == 200) {
        this.service.showSuccess("verify");
      }
      else {
        console.log("else")
        this.service.showError('link expired')
        this.navi.navigate(['passwordChanged'])
      }
    },error=>{
      console.log('error occur', error)
      this.service.showError('Server Error')
    }
    )
  }
}
